// React Components
import React from 'react';

// Antd Components
import { Layout, Menu, Icon, Avatar } from 'antd';

// Console CSS
import './console.css'

// Constants
import * as Cons from './../../helpers/constants.js'
const { Header, Content, Footer, Sider } = Layout;
const SubMenu = Menu.SubMenu;

// Risen Console
export class Console extends React.Component {

  //--------------------------------------------------
  //  Class Properties
  //--------------------------------------------------

  state = {
    collapsed: false,
    viewWidth: '0',
    viewHeight: '0'
  };

  //--------------------------------------------------
  //  Constructor
  //--------------------------------------------------

  constructor(props) {
    super(props);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  //--------------------------------------------------
  //  Resizing Listener
  //--------------------------------------------------

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ viewWidth: window.innerWidth, viewHeight: window.innerHeight });
  }

  //--------------------------------------------------
  //  Sidemenu Toggle
  //--------------------------------------------------

  onCollapse = (collapsed) => {
    this.setState({ collapsed });
  }

  //--------------------------------------------------
  //  Console Renderer
  //--------------------------------------------------

  render() {

    // Minimum content height for the screen.
    const minContentHeight = this.state.viewHeight - (Cons.headerHeight + Cons.footerHeight)

    return (
      <Layout className='layout'>
        {/* Sidemenu */}
        <Sider className='sidemenu' collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
          {/* Navigation */}
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            {/* Dashboard */}
            <Menu.Item key="1">
              <Icon type="home" />
              <span>Dashboard</span>
            </Menu.Item>
            {/* Team Manager */}
            <Menu.Item key="2">
              <Icon type="solution" />
              <span>Team Manager</span>
            </Menu.Item>
            {/* Leagues */}
            <SubMenu key="3" title={<span><Icon type="fork"/><span>Leagues</span></span>}>
              {/* Brawler League */}
              <SubMenu key="league1" title="Brawler League">
                <Menu.Item key="b1">Teams</Menu.Item>
                <Menu.Item key="b2">Schedule</Menu.Item>
                <Menu.Item key="b3">Standings</Menu.Item>
              </SubMenu>
              {/* Duelist League */}
              <SubMenu key="league2" title="Duelist League">
                <Menu.Item key="d1">Teams</Menu.Item>
                <Menu.Item key="d2">Schedule</Menu.Item>
                <Menu.Item key="d3">Standings</Menu.Item>
              </SubMenu>
              {/* Master League */}
              <SubMenu key="league3" title="Master League">
                <Menu.Item key="m1">Teams</Menu.Item>
                <Menu.Item key="m2">Schedule</Menu.Item>
                <Menu.Item key="m3">Standings</Menu.Item>
              </SubMenu>
            </SubMenu>
            {/* Help */}
            <SubMenu key="5" title={<span><Icon type="question-circle-o"/><span>Help</span></span>}>
              <Menu.Item key="h1">FAQ</Menu.Item>
              <Menu.Item key="h2">Rules</Menu.Item>
            </SubMenu>
          </Menu>
        </Sider>
        <Layout className='layout'>
          {/* Header */}
          <Header className='header' style={{background: Cons.headerColor, height: Cons.headerHeight}}>
            {/* Logo */}
            <h1 className='logo'><b>Risen eSports</b></h1>
            {/* Avatar Picture */}
            <Avatar className='avatar' src='http://ddragon.leagueoflegends.com/cdn/6.24.1/img/profileicon/984.png' size="large" icon="user"/>
            {/* Username */}
            <p className='user'>Zanchee</p>
          </Header>
          {/* Content */}
          <Content className='content'>
            {/* Current Tab */}
            <div className='contentContainer' style={{background: Cons.contentColor, minHeight: minContentHeight}}>
              content
            </div>
            {/* Footer */}
            <Footer className='footer' style={{height: Cons.footerHeight}}>
              {Cons.footerContent}
            </Footer>
          </Content>
        </Layout>
      </Layout>
    );
  }
}



